<template>
    <div class="trace-analysis">
        <van-loading v-if="loading" class="trace-loading" type="spinner" color="#1989fa" size="40px" />
        <!-- 头部 -->
        <div class="trace-analysis-header" v-else>
            <!-- 标题 -->
            <div class="trace-analysis-title">
                <p>
                    <img class="analysis-title-img" :src="titleLIcon" />
                    <span class="analysis-title-text">{{ name }}</span>
                    <img class="analysis-title-img" :src="titleRIcon" />
                </p>
            </div>
            <!-- idiscode -->
            <div class="trace-analysis-idisCode">
                <!-- 副标题 -->
                <div class="analysis-idisCode-label">
                    <img class="idisCode-label-img subTitle" :src="arrowIcon" />
                    <span class="idisCode-label-text subText">国家工业互联网标识码</span>
                </div>
                <!-- 码量信息 -->
                <div class="analysis-idisiCode-info">
                    <span class="idisiCode-info-text">{{ idisCode }}</span>
                </div>
            </div>
            <!-- tab -->
            <div class="trace-analysis-tab">
                <div class="trace-analysis-empty" v-if="isEmpty">
                    <p><img style="width: 50%;" :src="emptyImg" /></p>
                    <p style="margin: 10px;letter-spacing: 1px;color: #606266;"><span>暂无数据</span></p>
                </div>
                <van-tabs color="#185EF0" v-else>
                    <van-tab v-for="(element, eleIndex) in tabList" :key="eleIndex" :title="element.tab">
                        <div
                            class="trace-analysis-img"
                            :style="item.isLabel ? `margin:0` : undefined"
                            v-for="(item, index) in element.processArr"
                            :key="index"
                        >
                            <!-- 副标题 -->
                            <template v-if="!item.isLabel">
                                <div class="analysis-img-label">
                                    <div class="img-label-top">
                                        <img class="subTitle" :src="arrowIcon" />
                                        <template v-if="item.span">
                                            <label class="subText">{{ item.label }}:</label>
                                            <span class="subText">{{ item.span }}</span>
                                        </template>
                                        <template v-else>
                                            <label class="subText">{{ item.label }}</label>
                                        </template>
                                        <template v-if="item.subExtraTitle">
                                            <div class="subExtraTitle">
                                                <template v-if="item.subExtraTitle >= 500">
                                                    <span>海拔500m以上</span>
                                                </template>
                                                <template v-else>
                                                    <span>海拔500m以下</span>
                                                </template>
                                            </div>
                                        </template>
                                    </div>
                                    <div class="img-label-bottom">
                                        <p v-for="(subItem, subIndex) in item.labels" :key="subIndex">
                                            <img :src="subItem.icon" />
                                            <label>{{ subItem.label }}:&nbsp;</label>
                                            <span>{{ subItem.span }}</span>
                                        </p>
                                    </div>
                                </div>
                            </template>
                            <!-- 图片 -->
                            <div class="analysis-img-item">
                                <template v-if="item.isImg">
                                    <p class="agriculteImg"><img :src="agriculteInfoTitle" /></p>
                                    <p class="agriculteImg agriculteFlex">
                                        <img
                                            v-for="(agriculteItem, agriculteIndex) in agriculteArrs"
                                            :key="agriculteIndex"
                                            :src="agriculteItem.icon"
                                        />
                                    </p>
                                    <p class="agriculteImg" v-for="(subItem, subIndex) in item.imgs" :key="subIndex">
                                        <img :src="subItem" />
                                    </p>
                                </template>
                                <template v-else>
                                    <p
                                        class="agriculteOImg"
                                        :style="
                                            ['生产加工', '农事信息'].includes(element.tab)
                                                ? `padding: 20px 10px`
                                                : undefined
                                        "
                                        v-for="(subItem, subIndex) in item.imgs"
                                        :key="subIndex"
                                    >
                                        <img :src="subItem" />
                                    </p>
                                </template>
                            </div>
                        </div>
                    </van-tab>
                </van-tabs>
            </div>
        </div>
    </div>
</template>
<script>
import emptyImg from '@/assets/trace/empty.png'
import flIcon from '@/assets/trace/fl-icon.png'
import titleLIcon from '@/assets/trace/decorationL-icon.png'
import titleRIcon from '@/assets/trace/decorationR-icon.png'
import arrowIcon from '@/assets/trace/arrow-icon.png'
import timeIcon from '@/assets/trace/time-icon.png'
import teaNameIcon from '@/assets/trace/teaName-icon.png'
import teaAreaIcon from '@/assets/trace/teaArea-icon.png'
import teaAddressIcon from '@/assets/trace/teaAddress-icon.png'
import seleniumIcon from '@/assets/trace/selenium-icon.png'
import locationIcon from '@/assets/trace/location-icon.png'
import cygIcon from '@/assets/trace/cyg-icon.png'
import flmcIcon from '@/assets/trace/flmc-icon.png'
import ftTimeIcon from '@/assets/trace/ftTime-icon.png'
import ggsyIcon from '@/assets/trace/ggsy-icon.png'
import xjTimeIcon from '@/assets/trace/xjTime-icon.png'
import djIcon from '@/assets/trace/dj-icon.png'
import weightIcon from '@/assets/trace/weight-icon.png'
// 生产加工
import productProcessBackscjgl from '@/assets/trace/consumer/product_process/process_scjgl_back.png'
import productProcessBackscjgh from '@/assets/trace/consumer/product_process/process_scjgh_back.png'
// 农事信息
import agriculteInfoBack from '@/assets/trace/consumer/agriculte_info/agriculte_back.png'
import agriculteInfoTitle from '@/assets/trace/consumer/agriculte_info/agriculte_title.png'
import agriculteJfIcon from '@/assets/trace/consumer/agriculte_info/agriculte_jf_icon.png'
import agriculteRgccIcon from '@/assets/trace/consumer/agriculte_info/agriculte_rgcc_icon.png'
import agriculteRgftIcon from '@/assets/trace/consumer/agriculte_info/agriculte_rgft_icon.png'
import agriculteRgggIcon from '@/assets/trace/consumer/agriculte_info/agriculte_rggg_icon.png'
import agriculteRgxjIcon from '@/assets/trace/consumer/agriculte_info/agriculte_rgxj_icon.png'
import agriculteYjfIcon from '@/assets/trace/consumer/agriculte_info/agriculte_yjf_icon.png'
// 接口请求方法
import { getJsonResult } from '@/utils/server-wrapper'
export default {
    components: {},
    data() {
        return {
            loading: true,
            isEmpty: false,
            emptyImg,
            djIcon,
            weightIcon,
            flIcon,
            titleLIcon,
            titleRIcon,
            arrowIcon,
            timeIcon,
            teaNameIcon,
            teaAreaIcon,
            teaAddressIcon,
            seleniumIcon,
            locationIcon,
            cygIcon,
            flmcIcon,
            ftTimeIcon,
            ggsyIcon,
            xjTimeIcon,
            name: '',
            idisCode: '',
            tabList: [],
            agriculteInfoTitle,
            agriculteArrs: [
                {
                    // 人工除草
                    key: 1,
                    icon: agriculteRgccIcon
                },
                {
                    // 人工修剪
                    key: 2,
                    icon: agriculteRgxjIcon
                },
                {
                    // 人工翻土
                    key: 3,
                    icon: agriculteRgftIcon
                },
                {
                    // 有机肥
                    key: 4,
                    icon: agriculteYjfIcon
                },
                {
                    // 钾肥
                    key: 5,
                    icon: agriculteJfIcon
                },
                {
                    // 人工灌溉
                    key: 6,
                    icon: agriculteRgggIcon
                }
            ]
        }
    },
    methods: {
        // 生产加工
        traceTeaProduceListFunc(arr, traceTeaProduceList) {
            if (traceTeaProduceList) {
                arr.push({
                    tab: '生产加工',
                    processArr: [
                        {
                            label: '产线名称',
                            span: traceTeaProduceList[traceTeaProduceList.length - 1].productLing,
                            labels: [
                                {
                                    icon: timeIcon,
                                    label: '完成时间',
                                    span: traceTeaProduceList[traceTeaProduceList.length - 1].finishTime
                                }
                            ],
                            imgs:
                                traceTeaProduceList.length == 10 ? [productProcessBackscjgh] : [productProcessBackscjgl]
                        }
                    ].concat(
                        traceTeaProduceList.map(item => {
                            let labels = [
                                {
                                    icon: timeIcon,
                                    label: '完成时间',
                                    span: item.finishTime
                                },
                                {
                                    icon: cygIcon,
                                    label: '茶园工',
                                    span: item.executorName
                                },
                                {
                                    icon: weightIcon,
                                    label: '茶青重量',
                                    span: item.weight ? `${item.weight}kg` : null
                                },
                                {
                                    icon: weightIcon,
                                    label: '成茶重量',
                                    span: item.finishedWeight ? `${item.finishedWeight}kg` : null
                                }
                            ]
                            return item.productProcess
                                ? {
                                    label: item.productProcess,
                                    labels: [
                                        {
                                            icon: teaNameIcon,
                                            label: '产线名称',
                                            span: item.productLing
                                        }
                                    ]
                                        .concat(labels)
                                        .filter(ele => ![null, ''].includes(ele.span)),
                                    imgs: item.imageUrlList ? item.imageUrlList : item.marketingImageList
                                }
                                : {
                                    label: '产线名称',
                                    span: item.productLing,
                                    labels: labels.filter(ele => ![null, ''].includes(ele.span)),
                                    imgs: item.imageUrlList ? item.imageUrlList : item.marketingImageList
                                }
                        })
                    )
                })
            }
        },
        // 茶园信息
        traceTeaGardenFunc(arr, traceTeaGarden) {
            if (traceTeaGarden) {
                arr.push({
                    tab: '茶园信息',
                    processArr: [
                        {
                            label: '所属企业',
                            span: traceTeaGarden['enterpriseName'],
                            subExtraTitle: traceTeaGarden['altitude'],
                            labels: [
                                {
                                    icon: teaNameIcon,
                                    label: '茶园名称',
                                    span: traceTeaGarden['teaGardenName']
                                },
                                {
                                    icon: teaAreaIcon,
                                    label: '茶园面积',
                                    span: traceTeaGarden['teaGardenArea']
                                        ? `${traceTeaGarden['teaGardenArea']}亩`
                                        : null
                                },
                                {
                                    icon: teaAddressIcon,
                                    label: '茶园地址',
                                    span: traceTeaGarden['teaGardenAddress']
                                },
                                {
                                    icon: seleniumIcon,
                                    label: '硒含量',
                                    span: traceTeaGarden['selenium'] ? `${traceTeaGarden['selenium']}mg/kg` : null
                                },
                                {
                                    icon: locationIcon,
                                    label: '经纬度',
                                    span: traceTeaGarden['latitudeLongitude']
                                }
                            ].filter(ele => ![null, ''].includes(ele.span)),
                            imgs: traceTeaGarden['imageUrlList']
                                ? traceTeaGarden['imageUrlList']
                                : traceTeaGarden['marketingImageList']
                        }
                    ]
                })
            }
        },
        // 茶青采摘
        traceTeaPickFunc(arr, traceTeaPick) {
            if (traceTeaPick) {
                arr.push({
                    tab: '茶青采摘',
                    processArr: [
                        {
                            label: '采摘品种',
                            span: traceTeaPick['pickBreed'],
                            labels: [
                                {
                                    icon: timeIcon,
                                    label: '采摘时间',
                                    span: traceTeaPick['pickTime']
                                },
                                {
                                    icon: teaNameIcon,
                                    label: '采摘要求',
                                    span: traceTeaPick['pickRequire']
                                },
                                {
                                    icon: cygIcon,
                                    label: '茶园工',
                                    span: traceTeaPick['executorName']
                                },
                                {
                                    icon: weightIcon,
                                    label: '茶青重量',
                                    span: traceTeaPick['weight'] ? `${traceTeaPick['weight']}kg` : null
                                },
                                {
                                    icon: djIcon,
                                    label: '茶叶品级',
                                    span: traceTeaPick['breedNum']
                                }
                            ].filter(ele => ![null, ''].includes(ele.span)),
                            imgs: traceTeaPick['imageUrlList']
                                ? traceTeaPick['imageUrlList']
                                : traceTeaPick['marketingImageList']
                        }
                    ]
                })
            }
        },
        // 产品包装
        traceTeaPackageFunc(arr, traceTeaPackage) {
            if (traceTeaPackage) {
                arr.push({
                    tab: '产品包装',
                    processArr: [
                        {
                            label: '生产批次码',
                            span: traceTeaPackage['productBatchCode'],
                            labels: [
                                {
                                    icon: timeIcon,
                                    label: '完成时间',
                                    span: traceTeaPackage['finishTime']
                                },
                                {
                                    icon: cygIcon,
                                    label: '包装工',
                                    span: traceTeaPackage['executorName']
                                }
                            ].filter(ele => ![null, ''].includes(ele.span)),
                            imgs: traceTeaPackage['imageUrlList']
                                ? traceTeaPackage['imageUrlList']
                                : traceTeaPackage['marketingImageList']
                        }
                    ]
                })
            }
        },
        // 农事信息
        farmingListFunc(arr, farmingList) {
            if (farmingList) {
                arr.push({
                    tab: '农事信息',
                    processArr: [
                        {
                            isLabel: true,
                            isImg: true,
                            imgs: [agriculteInfoBack]
                        }
                    ].concat(
                        farmingList.map(item => {
                            return {
                                label: item.farmingTypeDesc,
                                span: item.farmingProcessType,
                                labels: [
                                    {
                                        key: 'farmingTime',
                                        icon: timeIcon,
                                        name: '农事时间',
                                        value: item.farmingTime
                                    },
                                    {
                                        key: 'fertilizerName',
                                        icon: flmcIcon,
                                        name: '肥料名称',
                                        value: item.fertilizerName
                                    },
                                    {
                                        key: 'dosage',
                                        icon: teaNameIcon,
                                        name: '使用量',
                                        value: item.dosage ? `${item.dosage}kg` : null
                                    },
                                    {
                                        key: 'pesticideName',
                                        icon: teaNameIcon,
                                        name: '农药名称',
                                        value: item.pesticideName
                                    },
                                    {
                                        key: 'irrigateWaterSource',
                                        icon: ggsyIcon,
                                        name: '灌溉水源',
                                        value: item.irrigateWaterSource
                                    },
                                    {
                                        key: 'executorName',
                                        icon: cygIcon,
                                        name: '茶园工',
                                        value: item.executorName
                                    }
                                ]
                                    .map(item => {
                                        return { icon: item.icon, label: item.name, span: item.value }
                                    })
                                    .filter(ele => ![null, ''].includes(ele.span)),
                                imgs: item.imageUrlList ? item.imageUrlList : item.marketingImageList
                            }
                        })
                    )
                })
            }
        },
        // 质量检验
        traceTeaQualityFunc(arr, traceTeaQuality) {
            if (traceTeaQuality) {
                arr.push({
                    tab: '质量检验',
                    key: 7,
                    processArr: [
                        {
                            label: '生产批次码',
                            span: traceTeaQuality['productBatchCode'],
                            labels: [
                                {
                                    key: 'finishTime',
                                    name: '完成时间',
                                    icon: timeIcon,
                                    value: traceTeaQuality.finishTime
                                },
                                {
                                    key: 'executorName',
                                    name: '检验员',
                                    icon: cygIcon,
                                    value: traceTeaQuality.executorName
                                }
                            ]
                                .map(item => ({ icon: item.icon, label: item.name, span: item.value }))
                                .filter(ele => ![null, ''].includes(ele.span)),
                            imgs: traceTeaQuality['imageUrlList']
                                ? traceTeaQuality['imageUrlList']
                                : traceTeaQuality['marketingImageList']
                        }
                    ]
                })
            }
        },
        async traceSource(idisCode) {
            this.loading = true
            let result = await getJsonResult(`/wx/trace-source?idisCode=${idisCode}`)
            let {
                code,
                objectName,
                traceTeaProduceList,
                traceTeaGarden,
                traceTeaPick,
                traceTeaPackage,
                farmingList,
                traceTeaQuality
            } = result
            if (result) this.loading = false
            this.name = objectName
            this.idisCode = code
            /**
             * @params
             * traceTeaProduceList  生产加工
             * traceTeaGarden  茶园信息
             * traceTeaPick  茶青采摘
             * traceTeaPackage  产品包装
             * farmingList  农事信息
             * traceTeaQuality  质量检验
             */
            if (
                traceTeaGarden == null &&
                farmingList == null &&
                traceTeaPick == null &&
                traceTeaProduceList == null &&
                traceTeaQuality == null &&
                traceTeaPackage == null
            ) {
                this.isEmpty = true
            }
            // 茶园信息
            this.traceTeaGardenFunc(this.tabList, traceTeaGarden)
            // 农事信息
            this.farmingListFunc(this.tabList, farmingList)
            // 茶青采摘
            this.traceTeaPickFunc(this.tabList, traceTeaPick)
            // 生产加工
            this.traceTeaProduceListFunc(this.tabList, traceTeaProduceList)
            // 质量检验
            this.traceTeaQualityFunc(this.tabList, traceTeaQuality)
            // 产品包装
            this.traceTeaPackageFunc(this.tabList, traceTeaPackage)
        }
    },
    mounted() {
        document.title = '产品溯源'
        if (this.$route.query.idisCode) {
            console.log('this.query.idisCode', this.$route.query.idisCode)
            this.traceSource(this.$route.query.idisCode)
        }
    }
    // async beforeRouteEnter(to, from, next) {
    //     document.title = '产品溯源'
    // }
}
</script>
<style lang="scss" scoped>
.trace-analysis {
    height: 100vh;
    position: relative;
    overflow: auto;
}

.trace-loading {
    position: absolute;
    top: calc(50% - 25px);
    left: calc(50% - 25px);
}

.trace-analysis-header {
    background: #fff;
}

.trace-analysis-title > p {
    padding: 20px 10px;
}

.analysis-title-img {
    padding: 10px;
}

.analysis-title-text {
    padding: 10px;
    font-size: 1.3rem;
    vertical-align: 12px;
    font-weight: 600;
    letter-spacing: 1px;
}

.trace-analysis-idisCode {
    text-align: left;
    padding: 0 20px;
}

.idisCode-label-img,
.subTitle {
    width: 20px;
}

.idisCode-label-text,
.subText {
    padding: 0 0 0 10px;
    font-size: 0.9rem;
    vertical-align: 3px;
    font-weight: 600;
    letter-spacing: 1px;
    word-break: break-all;
}

.subExtraTitle {
    padding: 10px 0 0;
    color: #2362e0;
    letter-spacing: 1px;
    font-weight: 600;
}

.agriculteImg {
    padding: 20px 16px;
    background: #ecf8ff;
    margin: 0 !important;
}

.agriculteOImg {
    padding: 20px 0;
    background: #ecf8ff;
    margin: 0 !important;
}

.agriculteFlex {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.agriculteFlex > img {
    width: calc(100% / 3);
    transform: scale(0.7);
}

.analysis-idisiCode-info {
    margin: 20px 0px;
    padding: 15px 0;
    background: #e6f0f9;
    border-radius: 35px;
    text-align: center;
    font-size: 0.85rem;
}

.trace-analysis-tab {
    background: #f5f5f5;
}

.trace-analysis-img {
    margin: 10px 0 0;
}

.analysis-img-label {
    // margin: 20px 0 0;
    padding: 20px 20px 0;
    text-align: left;
    background: #fff;
}

.img-label-top {
    padding: 0 0 10px 0;
    border-bottom: 1px solid #eeeeee;
}

.img-label-bottom {
    padding: 10px 0;
}

.img-label-bottom > p {
    margin: 0 0 15px;
    font-size: 1rem;
}

.img-label-bottom > p > img {
    width: 1rem;
    vertical-align: -3px;
}

.img-label-bottom > p > label {
    padding: 0 0 0 10px;
    letter-spacing: 1px;
}

.analysis-img-item > p {
    margin: 0 0 10px 0;
}

.analysis-img-item > p:last-child {
    margin: 0;
}

.analysis-img-item > p > img {
    display: block;
}
</style>
